import React from "react";
import { BrowserRouter, NavLink, Route } from "react-router-dom";
import { Routes } from "react-router";
import Produtos from "./Pages/Produtos";
import Contato from "./Pages/Contato";

import "./styles/App.modules.css";
import Footer from "./Pages/Footer";
import NotFound from "./components/NotFound";
import Produto from "./Pages/Produto";

const App = () => {
  return (
    <div className="container">
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <nav className="navigator">
          <NavLink to="reactOrigamid"  className="button">
            Produtos
          </NavLink>
          <NavLink to="contato" className="button">
            Contato
          </NavLink>
        </nav>
        <Routes>
          <Route path="reactOrigamid"  element={<Produtos />} />
          <Route path="reactOrigamid/contato" element={<Contato />} />
          <Route path="reactOrigamid/produto/:id/*" element={<Produto />} />
          <Route path="reactOrigamid/*" element={<NotFound />} />
        </Routes>
      <Footer />
        
      </BrowserRouter>
    </div>
  );
};

export default App;
